.app_wrapper{
  width: 71%;
  margin-left: 14.5%;
  font-family: 'Open Sans', sans-serif;
  /* position: absolute; */
  max-width: 1170px;
}

@media(min-width: 2000px){
.app_wrapper{
margin-left: 20%;
} 
}
