.desc_wrap{
margin-left: 20px;
display: flex;
justify-content: space-around;
}
.desc_wrap img{
    width: 350px;
}
h1{
    text-align: center;
    font-size: 50px;
    line-height: 54px;
    font-weight: 400;
}
h2{
    font-size: 24px;
    line-height: 29px;
}
.desc_wrap_text{
width: 60%;
margin-left: 30px;
}
.p_wrapper, .sign{
font-size: 16px;
line-height: 24px;
}
.sign{
color: rgb(239, 91, 76);
font-weight: bold;
}
.sign:hover{
    cursor: pointer;
    color: rgb(201, 54, 0);
}