.desc_wrap{
    text-align: center;
}
.desc_wrap p{
font-size: 16px;
line-height: 24px;
}
.desc_wrap h1{
    font-size: 50px;
    line-height: 54px;
}

.input_wrap{
    display: grid;
    justify-content: center;

}
.input_wrap input{
    width: 450px;
    height: 35px;
    border-radius: 5px;
    border: 0.5px solid rgb(210, 210, 210);
    font-size: 16px;
    line-height: 24px;

}
.radio_p_wrap{
    font-size: 16px;
    line-height: 24px;
    display: grid;
    justify-content: center;
}
.radio_wrap {
    display: grid;
    grid-template-columns: 25px 1px;
    grid-gap: 5px;

}
.radio_wrap div{
    width: 200px;
}

.load_photo{
display: flex;
}
.upload{
line-height: 35px;
outline: unset;
font-size: 16px;
line-height: 24px
}
.load_photo input.upload{ 
    border-radius: 5px 0 0 5px;
    width: 400px;
    height: 35px;
    border: 0.5px solid rgb(210, 210, 210);
    display: flex;
    justify-content: space-between;
z-index: 3;
background-color: white;
}
.load_photo button{
    border-radius: 0 5px 5px 0;
background-color:rgba(210, 210, 210, 0.534) ;
/* position: relative;
left: -401px; */
}
.sign{
   
        background-color: rgb(239, 91, 76);
        outline: none;
        border: unset;
        border-radius: 5px;
        width: 210px;
        height: 40px;
        color: white;
        font-size: 17px;
        font-weight: 400;
        margin-top: 20px;
    
}
.sign:hover{
    cursor: pointer;
    background-color: rgb(201, 54, 0);
}
.button_wrap{
    display: flex;
    justify-content: center;
}
