.sign{
    background-color: rgb(239, 91, 76);
    outline: none;
    border: unset;
    border-radius: 5px;
    width: 210px;
    height: 40px;
    color: white;
    font-size: 17px;
    font-weight: 400;
}
.sign:hover{
    cursor: pointer;
    background-color: rgb(201, 54, 0);
}
.block3{
    background-color: #f9f9f3;
}
.block3 p{
    text-align: center;
}
.button_wrap{
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
.human_wrap{
    width: 200px;
    height: 300px;
    background-color: peachpuff; /* временно */
    display: flex;
    flex-direction: column;
    align-items: center;
}
.human_wrap img{
    width: 80px;
    margin-top: 20px;
    border-radius: 40px;
    /* height: 120px; */
}
.human_wrap h2{
    margin-top: 15px;
    text-align: center;

}
.human_wrap p{
    margin-top: -5px;
}

.user_wrap{
    width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;  
  grid-template-rows: 1fr 1fr ;
  grid-gap: 20px;
  margin-left: 12.5%;

}