.block1 img{
width: 100%;
    margin-top: 60px;
}
.desc_wrap{
    position: relative;
    top: -540px;
    left: 30px;
    width: 60%;
    color: white;
    
}

.sign{
    background-color: rgb(239, 91, 76);
    outline: none;
    border: unset;
    border-radius: 5px;
    width: 210px;
    height: 40px;
    color: white;
    font-size: 17px;
    font-weight: 400;
    margin-top: 20px;
}
.sign:hover{
    cursor: pointer;
    background-color: rgb(201, 54, 0);
}
.desc_wrap h1{
    font-size: 50px;
line-height: 54px;
font-weight: 400;
text-align: start;
}
.desc_wrap p{
    margin-top: -20px;
    font-size: 16px;
    line-height: 24px;
}

@media(max-width:1200px){
    .desc_wrap{
        top: -450px;
    }
    .desc_wrap{
        width: 80%;
    }
}