.header{
    width: 71%;
    height: 60px;
    font-size: 14px;
    color: rgb(31, 30, 30);
    display: flex;
justify-content: space-between;
 position: fixed;
 background-color: rgba(255, 255, 255, 0.74);
 max-width: 1170px;
}
.img_wrap, .nav_wrap{

    margin: 20px 30px 0 30px;
}
.mob_wrap, .menu_button, .menu_logo{
  display: none;
}

.nav_wrap{
  width: 45%;
  display: flex;
  justify-content: space-around;  
  color: black;
}
.nav_wrap a{
color: black;
  text-decoration: none;

}
.nav_wrap a.activeLink{
  color: darkblue;
}


.mob_nav{
  position: absolute;
  height: auto + 'px';
  width: 200px;
  top: 0;
  left: 0;
}


@media(max-width: 1200px){
  .header{
    width: 70%;
    display: flex;
justify-content: space-between;
  }
  .img_wrap{
    min-width: 30%;
  }
  .nav_wrap{
    margin-left: 30%;
    width: 85%;
    display: flex;
    justify-content: space-around;
  }
  .nav_wrap a, span{
margin-left: 30px;
  }

}
@media(max-width:900px){
  .header{
    width: 100%;
  }
  .nav_wrap > a, span{
display: none;
  }
  .mob_wrap, .menu_button, .menu_logo{
    display: initial;
  }
.mob_wrap{
  display: grid;
  grid-template-columns: 1fr;

}

}